.carousel{
    height: 100%;
}
.productDetails{
    width: 100vw;
    max-width: 100%;
    padding:4vmax;
    box-sizing: border-box;
    display: flex;
    height: 100vmin;
}

.productDetails > div{
    width:100%;
    
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    padding: 2vmax;
    box-sizing: border-box;
    /* border: 1px solid black; */
}

.productDetails > div:last-child{
    align-items: flex-start;
    padding-top: 3vmax;
}

.detailsBlock-1 > h2{
    color: rgb(54,54,54);
    font : 600 1.6vmax "Roboto";
}

.detailsBlock-1 > p {
color: rgba(54,54,54,0.582);
font: 200 0.8vmax cursive;
}

.detailsBlock-2{
    display: flex;
    justify-content: flex-start;
    align-items: center;
    border-top: 1px solid rgba(0,0,0,0.205);
    border-bottom: 1px solid rgba(0,0,0,0.205);
    width: 70%;
    padding: 1vmax 0;
}

.detailsBlock-2 > span{
color: rgba(54,54,54,0.582);
font: 200 0.8vmax cursive;
}

.detailsBlock-3{
    width: 70%;
}

.detailsBlock-3 > h1{
    color: rgba(17,17,17,0.795);
    font: 400 1.8vmax "Franklin Gothic Medium";
    margin: 1vmax 0;
}

.detailsBlock-3-1{
    display: flex;
    align-items: center;
}

.detailsBlock-3-1-1 > button {
    border: none;
    background-color: rgba(0,0,0,0.616);
    padding:0.5vmax;
    cursor: pointer;
    color: white;
    transition: all 0.5s;
    border-radius: 30%;

}

.detailsBlock-3-1-1 > button:hover{
    background-color: rgba(0,0,0,0.767);
}

.detailsBlock-3-1-1 > input{
    border: none;
    padding: 0.5vmax;
    width: 2vmax;
    text-align: center;
    outline: none;
    font: 400 0.9vmax "Roboto";
    color: rgba(0,0,0,0.74);
}

.detailsBlock-3-1 > button:last-child{
    border: none;
    color: white;
    cursor: pointer;
    transition: all 0.5s;
    background-color: tomato;
    outline: none;
    margin: 1vmax;
    padding: 0.5vmax 2vmax;
    font: 500 0.8vmax "Roboto";
    outline:none;
    border-radius: 20px;

}

.detailsBlock-3-1 > button:last-child:hover{
    background-color: rgb(214,84,61);
}

.detailsBlock-3 > p {
    border-top: 1px solid rgba(0, 0, 0, 0.205);
    border-bottom: 1px solid rgba(0, 0, 0, 0.205);
    padding: 1vmax 0;
    color: rgba(0, 0, 0, 0.651);
    font: 400 1vmax "Roboto";
    margin: 1vmax 0;
  }
  
  .detailsBlock-4 {
    color: rgba(0, 0, 0, 0.897);
    font: 500 1.2vmax sans-serif;
  }
  
  .detailsBlock-4 > p {
    color: rgba(0, 0, 0, 0.534);
    font: 300 0.9vmax sans-serif;
  }

  .submitReview {
    border: none;
    background-color: tomato;
    font: 500 0.7vmax "Roboto";
    border-radius: 20px;
    padding: 0.6vmax 2vmax;
    margin: 1vmax 0;
    color: white;
    cursor: pointer;
    transition: all 0.5s;
    outline: none;
  }
  .submitReview:hover {
    background-color: rgb(197, 68, 45);
    transform: scale(1.1);
  }

.reviewHeading{
    font: 500 1.4vmax 'Roboto';
    border-bottom: 1px solid rgb(0,0,0,0.226);
    padding: 1vmax;
    width: 20vmax;
    margin: auto;
    text-align: center;
    margin-bottom: 4vmax;
    color: #000000be;
}

.reviews{
    display: flex;
    overflow: auto;
}

.reviewCard{
    flex: none;

    box-shadow: 0 0 5px rgba(0,0,0,0.226);
    border: 1px solid rgba(0,0,0,0.116);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 30vmax;
    margin: 1vmax;
    padding: 3vmax;
}

.reviewCard > img{
    width: 5vmax;
}

.reviewCard > p{
    color: rgba(0,0,0,0.836);
    font: 600 1vmax "Roboto";
}

.reviewCard > span{
    color: rgba(0,0,0,0.445);
    font: 300 0.9vmax cursive;
}

.noReviews{
    font: 400 1.3vmax "Gill Sans";
    text-align: center;
    color: rgba(0,0,0,0.548);
}

  @media screen and (max-width: 600px) {
    .productDetails {
        flex-direction: column;
        height: unset;
      }
    
      .productDetails > div:last-child {
        align-items: center;
        justify-content: flex-start;

      }
    
      .detailsBlock-1 > h2 {
        font-size: 2.8vmax;
        text-align: center;
      }
    
      .detailsBlock-1 > p {
        text-align: center;
        font-size: 1vmax;
      }
    
      .detailsBlock-2 {
        justify-content: center;
      }
      .detailsBlock-2 > span {
        font-size: 1.5vmax;
      }
    
      .detailsBlock-3 > h1 {
        font: 700 3vmax "Franklin Gothic Medium";
        text-align: center;
      }
    
      .detailsBlock-3-1 {
        flex-direction: column;
      }
    
      .detailsBlock-3-1-1 {
        padding: 2vmax 0;
      }
      .detailsBlock-3-1-1 > button {
        padding: 1.2vmax;
        width: 4vmax;
      }
    
      .detailsBlock-3-1-1 > input {
        width: 4vmax;
        font: 400 2vmax "Roboto";
      }
    
      .detailsBlock-3-1 > button:last-child {
        font: 500 1.7vmax "Roboto";
        padding: 1.5vmax;
        width: 20vmax;
        margin: 3vmax 0;
      }
    
      .detailsBlock-3 > p {
        padding: 2.5vmax 0;
        text-align: center;
        font: 400 2vmax "Roboto";
      }
    
      .detailsBlock-4 {
        font: 500 2.5vmax sans-serif;
      }
    
      .detailsBlock-4 > p {
        font: 300 1.8vmax sans-serif;
      }
    
      .submitReview {
        font: 500 1.7vmax "Roboto";
        padding: 1.5vmax;
        width: 20vmax;
        margin: 3vmax 0;
      }

      .reviewCard > p{
        font: 600 2vmax "Roboto";
    }
    
    .reviewCard > span{
        font: 300 1.5vmax cursive;
    }
}
